import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import 'rxjs/add/operator/map'
import {TimelineData} from "../models/timeline.model"
import {Observable} from "rxjs";

@Injectable(
)
export class DataService {
  private url="../assets/csvjson.json"
  private clock: Observable<Date>;


  constructor(private http: HttpClient) {
    //Every 1000 ms this assigns a new value to this.clock value
    this.clock = Observable.interval(1000).map(tick => new Date()).share();
  }

  getQuestions():Observable<TimelineData[]>{
    return this.http.get<TimelineData[]>(this.url)
  }

  getClock(): Observable<Date> {
    //If a function returns an observable, then if the value changes
    return this.clock;
  }

}



